import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import qiyeList from '@/views/pages/qiye/list'
import qiyeDetail from '@/views/pages/qiye/formModel'
import qiyeAdd from '@/views/pages/qiye/formAdd'
import gangweileixingList from '@/views/pages/gangweileixing/list'
import gangweileixingDetail from '@/views/pages/gangweileixing/formModel'
import gangweileixingAdd from '@/views/pages/gangweileixing/formAdd'
import zhaopinxinxiList from '@/views/pages/zhaopinxinxi/list'
import zhaopinxinxiDetail from '@/views/pages/zhaopinxinxi/formModel'
import zhaopinxinxiAdd from '@/views/pages/zhaopinxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import toudijianliList from '@/views/pages/toudijianli/list'
import toudijianliDetail from '@/views/pages/toudijianli/formModel'
import toudijianliAdd from '@/views/pages/toudijianli/formAdd'
import chayuejianliList from '@/views/pages/chayuejianli/list'
import chayuejianliDetail from '@/views/pages/chayuejianli/formModel'
import chayuejianliAdd from '@/views/pages/chayuejianli/formAdd'
import newsList from '@/views/pages/news/list'
import diquList from '@/views/pages/diqu/list'
import diquDetail from '@/views/pages/diqu/formModel'
import diquAdd from '@/views/pages/diqu/formAdd'
import wenjuanxinxiList from '@/views/pages/wenjuanxinxi/list'
import wenjuanxinxiDetail from '@/views/pages/wenjuanxinxi/formModel'
import wenjuanxinxiAdd from '@/views/pages/wenjuanxinxi/formAdd'
import yonghuhuidaList from '@/views/pages/yonghuhuida/list'
import yonghuhuidaDetail from '@/views/pages/yonghuhuida/formModel'
import yonghuhuidaAdd from '@/views/pages/yonghuhuida/formAdd'
import qiyedajuanList from '@/views/pages/qiyedajuan/list'
import qiyedajuanDetail from '@/views/pages/qiyedajuan/formModel'
import qiyedajuanAdd from '@/views/pages/qiyedajuan/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'qiyeList',
			component: qiyeList
		}, {
			path: 'qiyeDetail',
			component: qiyeDetail
		}, {
			path: 'qiyeAdd',
			component: qiyeAdd
		}
		, {
			path: 'gangweileixingList',
			component: gangweileixingList
		}, {
			path: 'gangweileixingDetail',
			component: gangweileixingDetail
		}, {
			path: 'gangweileixingAdd',
			component: gangweileixingAdd
		}
		, {
			path: 'zhaopinxinxiList',
			component: zhaopinxinxiList
		}, {
			path: 'zhaopinxinxiDetail',
			component: zhaopinxinxiDetail
		}, {
			path: 'zhaopinxinxiAdd',
			component: zhaopinxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'toudijianliList',
			component: toudijianliList
		}, {
			path: 'toudijianliDetail',
			component: toudijianliDetail
		}, {
			path: 'toudijianliAdd',
			component: toudijianliAdd
		}
		, {
			path: 'chayuejianliList',
			component: chayuejianliList
		}, {
			path: 'chayuejianliDetail',
			component: chayuejianliDetail
		}, {
			path: 'chayuejianliAdd',
			component: chayuejianliAdd
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'diquList',
			component: diquList
		}, {
			path: 'diquDetail',
			component: diquDetail
		}, {
			path: 'diquAdd',
			component: diquAdd
		}
		, {
			path: 'wenjuanxinxiList',
			component: wenjuanxinxiList
		}, {
			path: 'wenjuanxinxiDetail',
			component: wenjuanxinxiDetail
		}, {
			path: 'wenjuanxinxiAdd',
			component: wenjuanxinxiAdd
		}
		, {
			path: 'yonghuhuidaList',
			component: yonghuhuidaList
		}, {
			path: 'yonghuhuidaDetail',
			component: yonghuhuidaDetail
		}, {
			path: 'yonghuhuidaAdd',
			component: yonghuhuidaAdd
		}
		, {
			path: 'qiyedajuanList',
			component: qiyedajuanList
		}, {
			path: 'qiyedajuanDetail',
			component: qiyedajuanDetail
		}, {
			path: 'qiyedajuanAdd',
			component: qiyedajuanAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
